// __tests__/setup-testing-library.ts
import '@testing-library/jest-native/extend-expect';

// 타이머 모킹 (애니메이션 및 타이머 관련 테스트를 위해)
jest.useFakeTimers();

// PR_SHOULD_USE_CONTINUATION 속성을 window에 추가
// @ts-ignore - window 속성 무시
global.window = global.window || {};
// @ts-ignore - PR_SHOULD_USE_CONTINUATION 속성 추가
global.window.PR_SHOULD_USE_CONTINUATION = true;

// __DEV__가 이미 선언되어 있으므로 값만 설정
// @ts-ignore - __DEV__ 재선언 무시
global.__DEV__ = true;

// ReactTestInstance에 textContent 속성 추가 방식 수정
// @ts-ignore
if (typeof global.ReactTestInstance !== 'undefined') {
  // @ts-ignore
  Object.defineProperty(global.ReactTestInstance.prototype, 'textContent', {
    get: function(this: any) {
      return String(this.toString());
    }
  });
}

// react-native 모듈 모킹
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  
  function ViewComponent(props: any) {
    return props.children;
  }

  function TextComponent(props: any) {
    return props.children;
  }

  function TouchableOpacityComponent(props: any) {
    return props.children;
  }

  function ImageComponent(props: any) {
    return null;
  }

  function ScrollViewComponent(props: any) {
    return props.children;
  }

  function TextInputComponent(props: any) {
    return null;
  }
  
  return {
    ...RN,
    Alert: {
      alert: jest.fn()
    },
    View: ViewComponent,
    Text: TextComponent,
    TouchableOpacity: TouchableOpacityComponent,
    Image: ImageComponent,
    ScrollView: ScrollViewComponent,
    TextInput: TextInputComponent,
    Animated: {
      ...RN.Animated,
      timing: () => ({
        start: (callback?: any) => callback && callback(),
      }),
      spring: () => ({
        start: (callback?: any) => callback && callback(),
      }),
      View: ViewComponent,
      Text: TextComponent,
      Image: ImageComponent,
    },
    Platform: {
      ...RN.Platform,
      OS: 'android',
      select: (obj: any) => obj.android || obj.default,
    },
    Keyboard: {
      dismiss: jest.fn(),
      addListener: jest.fn(() => ({ remove: jest.fn() })),
    },
    NativeEventEmitter: jest.fn().mockImplementation(() => ({
      addListener: jest.fn(),
      removeListeners: jest.fn(),
    })),
  };
});